<template>
  <div class="hello">
    <!-- <div class="gag" @click="modal = false"></div> -->
    <div class="wrap">
      <header class="header">
        <img class="logo" :src="require('@/assets/imovers_logo.jpg')" alt="" />
        <div class="info-service">
          <div>tel: <a href="tel:0546324987">0546324987</a></div>
          <div>ז'בוטינסקי 11.פתח תקווה.</div>
        </div>
      </header>
      <h1>{{ msg }}</h1>
      <form class="info-user form-cargo">
        <div class="info-user__title title">Personal Info</div>
        <PersonalInfo
          v-for="infouser in INFOUSER"
          :key="infouser.id"
          :user_data="infouser"
        />
        <hr />
        <div class="items-list">
          <div class="title">Items List</div>
          <div class="items-list--row" v-for="(name, i) in ITEMLIST" :key="i" :data-index="name.id">
            <input
              class="form-control"
              v-model="ITEMLIST[i].name"
              @input="filterDate(name.name)"
              type="text"
              autocomplete="off"
              @focus="ITEMLIST[i].modal = true"
              :data-id="'name-' + name.id"
              ref="listOfItems"
            />
            <!-- @blur="ITEMLIST[i].modal = false"             -->
            <div class="list-arr" v-if="filterItems && ITEMLIST[i].modal" :data-id="i">
              <Items
                v-for="infoproducts in filteredDate"
                :key="infoproducts.id"
                :item_data="infoproducts"
                @setItem="setItem(infoproducts.name, i)"
              />
            </div>
            <div
              @click="addNewRow()"
              v-show="i == ITEMLIST.length - 1"
              class="add-more"
            >
              add more
            </div>
          </div>
        </div>
        <button class="btn">Send</button>
      </form>
    </div>
  </div>
</template>

<script>
import PersonalInfo from "@/components/PersonalInfo.vue";
import Items from "@/components/Items.vue";
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Info",
  components: {
    PersonalInfo,
    Items,
  },
  props: {
    msg: String,
  },
  data() {
    return {
      state: [],
      filteredDate: [],
    };
  },
  computed: {
    ...mapGetters(["INFOUSER", "INFOPRODUCTS", "FILTER_VALUE", "ITEMLIST"]),
  },
  methods: {
    ...mapActions(["GET_INFO_FROM_API", "GET_FILTER_VALUE", "GET_LIST_ITEM"]),
    filterDate(value) {
      this.GET_FILTER_VALUE(value);
    },
     listOfItems(value) {
       this.GET_LIST_ITEM(value);
    },
    filterItems(value) {
      let vm = this;
      vm.filteredDate = [...this.INFOPRODUCTS];
      if (value) {
        vm.filteredDate = vm.filteredDate.filter((item) => {
          return item.name.toLowerCase().includes(value.toLowerCase());
        });
      } else {
        vm.filteredDate = vm.INFOPRODUCTS;
      }
    },
    setItem(value,index) {
      let vm = this;
      
      vm.state = [...this.ITEMLIST];
      
      vm.state.forEach((item) => {
         item.modal = false;
      });
      vm.state[index].name = value
      vm.filteredDate = [];
    },
    addNewRow(name) {
      let vm = this;
      let id = vm.ITEMLIST.length
      vm.ITEMLIST.push({
        id: id,
        name: name,
        qty: "",
        disassemble: false,
        modal: false
      });
      this.filteredDate = [];
    },
  },
  watch: {
    FILTER_VALUE() {
      this.filterItems(this.FILTER_VALUE);
    },
    ITEMLIST(){
      this.setItem(this.ITEMLIST)
    },
  },
  mounted() {
    this.GET_INFO_FROM_API().then((response) => {
      this.filterItems(this.FILTER_VALUE);
      if (response.data) {
        console.log("data arrived");
      }
    });
  },
};
</script>

